            enable: true
          }
        },
        isShow: false,
        max_column: [255.304, 68.4, 286, 152.60333, 7306.137216, 100, 0.941906005, 100, 224.9, 84700, 125000, 13.9,
          0.979753846, 94.79932331, 0.92
        ],
        min_column: [4.8, 10.2, 9, 5, 16.8, 0.8, 0.003460297, 37, 2, 3425, 8900, 2.2, 0.743415385, 18.87109023,
          0.517804111
        ],
        avarage_column: [59.1518, 34.43, 71.4, 35.660333, 1272.263913, 84.28, 0.293885717, 79.2, 43.02535734,
          24064.001, 50640, 5.73, 0.855621538, 67.64942481, 0.688734784
        ],

        attribution: "<a href='https://maps.gsi.go.jp/development/ichiran.html' target='_blank'></a>",
        url: 'https://cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
        zoom: 3,

        radar_series: [{
          name: 'Tokyo',
          data: [0.90, 0.92, 0.86, 0.93],
        }, {
          name: 'Average cities',
          data: [0.77, 0.46, 0.46, 0.55],
        }],
        radar_chartOptions: {
          labels: ['Energy and Climate', 'City Plannig', 'Economic', 'Social Welfare'],
          title: {
            text: ''
          },
          dropShadow: {
            enabled: true,
            blur: 1,
            left: 1,
            top: 1
          },
          yaxis: {
            show: true,
            showAlways: true,
            tickAmount: 5,
            min: 0,
            max: 1.0,
            forceNiceScale: false,
            floating: false,
          }
        },
      }
    },
    created() {
      this.selected_city = "Tokyo";
      this.input_data = this.input_data.map(x => x.map(x => round_method(x, 3)));
      this.input_calculation = this.input_calculation.map(x => x.map(x => round_method(x, 3)));
      this.input_graph = this.input_graph.map(x => x.map(x => round_method(x, 3)));
    },
    methods: {
      reset_input_data: function () {
        location.reload();
      },
      select_city: function (event) {
        this.selected_city = event.target.value;
        this.selected_city_column = this.$refs.input_data_ref.table.getDataAtCol(2 + this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.selected_city_geodata = this.$refs.input_geodata_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].name = event.target.value
        this.radar_series[0].data = this.$refs.input_graph_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].data = this.radar_series[0].data.slice(1, 5).map(function (value) {
          return String(value);
        });
        this.Update();
      },
      Update: function () {
        var col_num = this.$refs.input_data_ref.table.getData()[0].length;
        var row_num = this.$refs.input_data_ref.table.getData().length;

        //input data
        this.matrix = transpose(this.input_data);
        //
        this.matrix = this.matrix[(2 + this.input_cities.findIndex(e =>
          e === this.selected_city_column[0]))];

        //
        this.max_column = MaxSort(this.max_column, this.selected_city_column.slice(1, this.selected_city_column
          .length));
        this.min_column = MinSort(this.min_column, this.selected_city_column.slice(1, this.selected_city_column
          .length));

        //
        this.before_matrix = this.$refs.input_data_ref.table.getData(0, 0, row_num - 1, 1 + (this.input_cities
          .findIndex(e =>
            e === this.selected_city_column[0])));
        this.after_matrix = this.$refs.input_data_ref.table.getData(0, 3 + (this.input_cities.findIndex(e =>
          e === this.selected_city_column[0])), row_num - 1, col_num - 3);
        //,Max,Min
        this.merged_matrix = pushTwoDimensionalArray(this.before_matrix, transpose([this.selected_city_column.map(
          function (value) {
            //String
            return String(value);
          })]), 1);
        this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, this.after_matrix, 1);

        //
        var average_array = [];
        var max_array = [];
        var min_array = [];
        for (var i = 1; i < row_num; i++) {
          average_array.push(average(this.merged_matrix[i].slice(2, col_num - 2)));
          max_array.push(Math.max.apply(null, this.merged_matrix[i].slice(2, col_num - 2)));
          min_array.push(Math.min.apply(null, this.merged_matrix[i].slice(2, col_num - 2)));
        }

        //
        var array2 = [];
        for (var j = 2; j < col_num - 2; j++) {
          array2.push(transpose(this.merged_matrix)[j]);
        }

        this.merged_matrix = pushTwoDimensionalArray(
          pushTwoDimensionalArray(transpose([transpose(this.merged_matrix)[0]]), transpose([
            ["Average 22 cities"].concat(average_array.map(function (element) {
              return parseFloat(element);
            }))
          ]), 1),
          transpose(array2), 1);

        this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
          ["Max"].concat(max_array.map(function (element) {
            return parseFloat(element);
          }))
        ]), 1);
        this.merged_matrix = (pushTwoDimensionalArray(this.merged_matrix, transpose([
          ["Min"].concat(min_array.map(function (element) {
            return parseFloat(element);
          }))
        ]), 1)).map(x => x.map(x => round_method(x, 3)));
        this.$refs.input_data_ref.table.loadData(this.merged_matrix);
        this.input_data = this.merged_matrix;

        // Input calculation;
        // GHG Emission
        var GHG = [];
        GHG = this.input_calculation[1].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[1].slice(1, col_num - 2)[index]), parseFloat(this[1].slice(col_num -
              2, col_num)[0]),
            parseFloat(this[1].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);
        GHG = ["GHG emissions (Mt)"].concat(GHG.map(function (element) {
          return String(element);
        }));

        // Nox
        var Nox = [];
        Nox = this.input_calculation[2].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[2].slice(1, col_num - 2)[index]), parseFloat(this[2].slice(col_num -
              2, col_num)[0]),
            parseFloat(this[2].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Nox = ["Annual mean NOx (ppm)"].concat(Nox.map(function (element) {
          return String(element);
        }));

        // PM10
        var PM10 = [];
        PM10 = this.input_calculation[3].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[3].slice(1, col_num - 2)[index]), parseFloat(this[3].slice(col_num -
              2, col_num)[0]),
            parseFloat(this[3].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        PM10 = ["Annual mean PM10 (ug/m3)"].concat(PM10.map(function (element) {
          return String(element);
        }));

        // PM25
        var PM25 = [];
        PM25 = this.input_calculation[4].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[4].slice(1, col_num - 2)[index]), parseFloat(this[4].slice(col_num -
              2, col_num)[0]),
            parseFloat(this[4].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        PM25 = ["Annual mean PM2.5 (ug/m3)"].concat(PM25.map(function (element) {
          return String(element);
        }));

        // Tec
        var Tec = [];
        Tec = this.input_calculation[5].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[5].slice(1, col_num - 2)[index]), parseFloat(this[5].slice(col_num -
              2, col_num)[0]),
            parseFloat(this[5].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Tec = ["Total energy consumption"].concat(Tec.map(function (element) {
          return String(element);
        }));

        // Energy and Climate
        var EaC = [];
        EaC = this.input_calculation[6].slice(1, col_num - 2).map(function (value, index) {
          return Geomean([parseFloat(GHG[index + 1]), parseFloat(Nox[index + 1]), parseFloat(PM10[index + 1]),
            parseFloat(PM25[
              index + 1]), parseFloat(Tec[index + 1])
          ])
        }, GHG, Nox, PM10, PM25, Tec);

        EaC = ["Energy and Climate"].concat(EaC.map(function (element) {
          return String(element);
        }));

        // Clean water accessibility (%)
        var Cwa = [];
        Cwa = this.input_calculation[7].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[6].slice(1, col_num - 2)[index]), parseFloat(this[6].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[6].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Cwa = ["Clean water accessibility (%)"].concat(Cwa.map(function (element) {
          return String(element);
        }));

        // Public transportation system (km/km2)
        var Pts = [];
        Pts = this.input_calculation[8].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[7].slice(1, col_num - 2)[index]), parseFloat(this[7].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[7].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Pts = ["Public transportation system (km/km2)"].concat(Pts.map(function (element) {
          return String(element);
        }));

        // Waste collection and management (%)
        var Wcam = [];
        Wcam = this.input_calculation[9].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[8].slice(1, col_num - 2)[index]), parseFloat(this[8].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[8].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Wcam = ["Waste collection and management (%)"].concat(Wcam.map(function (element) {
          return String(element);
        }));

        // Green area per capita (square meter) 
        var Gapc = [];
        Gapc = this.input_calculation[10].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[9].slice(1, col_num - 2)[index]), parseFloat(this[9].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[9].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Gapc = ["Green area per capita (square meter) "].concat(Gapc.map(function (element) {
          return String(element);
        }));

        // City Planning 
        var CP = [];
        CP = this.input_calculation[11].slice(1, col_num - 2).map(function (value, index) {
          return Geomean([parseFloat(Cwa[index + 1]), parseFloat(Pts[index + 1]), parseFloat(Wcam[index + 1]),
            parseFloat(Gapc[
              index +
              1])
          ].map(x => x))
        }, Cwa, Pts, Wcam, Gapc);

        CP = ["City Planning "].concat(CP.map(function (element) {
          return String(element);
        }));

        //Per capita GDP (USD)
        var GDP = [];
        GDP = this.input_calculation[12].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[10].slice(1, col_num - 2)[index]), parseFloat(this[10].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[10].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        GDP = ["Per capita GDP (USD)"].concat(GDP.map(function (element) {
          return String(element);
        }));

        //Labor productivity level (US$)
        var Lpl = [];
        Lpl = this.input_calculation[13].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[11].slice(1, col_num - 2)[index]), parseFloat(this[11].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[11].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Lpl = ["Labor productivity level (US$)"].concat(Lpl.map(function (element) {
          return String(element);
        }));

        //Unemployment rate (%)
        var Ur = [];
        Ur = this.input_calculation[14].slice(1, col_num - 2).map(function (value, index) {
          return Emission(parseFloat(this[12].slice(1, col_num - 2)[index]), parseFloat(this[12].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[12].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Ur = ["Unemployment rate (%)"].concat(Ur.map(function (element) {
          return String(element);
        }));

        // Economic  
        var Economic = [];
        Economic = this.input_calculation[15].slice(1, col_num - 2).map(function (value, index) {
          return Geomean([parseFloat(GDP[index + 1]), parseFloat(Lpl[index + 1]), parseFloat(Ur[index + 1])])
        }, GDP, Lpl, Ur);

        Economic = ["Economic "].concat(Economic.map(function (element) {
          return String(element);
        }));

        // Life expectancy index
        var Lei = [];
        Lei = this.input_calculation[16].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[13].slice(1, col_num - 2)[index]), parseFloat(this[13].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[13].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Lei = ["Life expectancy index"].concat(Lei.map(function (element) {
          return String(element);
        }));

        // Health Index 
        var HI = [];
        HI = this.input_calculation[17].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[14].slice(1, col_num - 2)[index]), parseFloat(this[14].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[14].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        HI = ["Health Index "].concat(HI.map(function (element) {
          return String(element);
        }));

        // Education Index 
        var EI = [];
        EI = this.input_calculation[18].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(parseFloat(this[15].slice(1, col_num - 2)[index]), parseFloat(this[15].slice(
              col_num - 2, col_num)[0]),
            parseFloat(this[15].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        EI = ["Education Index"].concat(EI.map(function (element) {
          return String(element);
        }));

        // Social Welfare  
        var SW = [];
        SW = this.input_calculation[19].slice(1, col_num - 2).map(function (value, index) {
          return (parseFloat(Lei[index + 1]) * parseFloat(HI[index + 1]) * parseFloat(EI[index + 1])) ** (0.333)
        }, Lei, HI, EI);

        SW = ["Social Welfare"].concat(SW.map(function (element) {
          return String(element);
        }));

        // USDI  
        var USDI = [];
        USDI = this.input_calculation[20].slice(1, col_num - 2).map(function (value, index) {
          return USDI_calc(parseFloat(EaC[index + 1]), parseFloat(CP[index + 1]), parseFloat(Economic[index + 1]),
            parseFloat(SW[
              index + 1]));
        }, EaC, CP, Economic, SW);

        USDI = ["USDI"].concat(USDI.map(function (element) {
          return String(element);
        }));
        this.input_calculation = [this.input_calculation[0], GHG, Nox, PM10, PM25, Tec, EaC,
          Cwa,
          Pts, Wcam, Gapc, CP, GDP, Lpl, Ur, Economic, Lei, HI, EI, SW, USDI
        ];
        this.input_calculation = this.input_calculation.map(x => x.map(x => round_method(x, 3)));
        this.$refs.input_calculation_ref.table.loadData(this.input_calculation);
        this.input_graph = [this.input_graph[0], EaC, CP, Economic, SW, USDI];
        this.input_graph = this.input_graph.map(x => x.map(x => round_method(x, 2)));
        this.ranking = transpose(transpose([this.input_graph[0], USDI.map(x => round_method(x, 2))]).slice(1, ).sort(
          function (
            a, b) {
            return (b[1] - a[1]);
          }));
        this.bar_chartOptions = {
          ...this.bar_chartOptions,
          ...{
            xaxis: {
              categories: this.ranking[0]
            }
          }
        }
        this.bar_chartOptions.xaxis.categories = this.ranking[0];
        this.bar_series[0].data = this.ranking[1];

        this.$refs.input_graph_ref.table.loadData(this.input_graph);
        var radar_data1 = [parseFloat(EaC[1]), parseFloat(CP[1]), parseFloat(Economic[1]),
          parseFloat(SW[1])
        ].map(x => round_method(x, 2))
        this.radar_series[1].data = radar_data1;
        var rader_index = this.input_graph[0].findIndex(e => e === this.selected_city_column[0]);
        var radar_data2 = [parseFloat(EaC[rader_index]), parseFloat(CP[rader_index]), parseFloat(
          Economic[rader_index]), parseFloat(SW[rader_index])].map(x => round_method(x, 2))
        this.radar_series[0].data = radar_data2;
      }
    },
    computed: {
      first_column_input_data: function () {
        return getCol(this.input_data, 0);
      },
      Max_column: function () {
        return getCol(this.input_data, this.input_data[0].length - 2);
      },
      Min_column: function () {
        return getCol(this.input_data, this.input_data[0].length - 1);
      },
      GHG: function () {
        return Emission(4.6, 10, 0.5);
      },
      mu: function () {
        return gmean([0.55, 0.69, 1, 0.98, 1]);
      },
      Social_Welfare: function () {
        return Social_Welfare(0.79, 0.80);
      },
    },

  };
</script>

